export default function getColor(gistItem) {
  switch (gistItem.categoryTitle) {
    case 'LINKEDIN':
    case 'FACEBOOK':
    case 'YOUTUBE':
    case 'INSTAGRAM':
    case 'TWITTER':
    case 'CINCIA':
    case 'TECNOLOGIA':
    case 'JOGOS':
    case 'POLITICA':
      return '#5fa9ff'

    case 'CARROS':
    case 'VIAGENS':
    case 'MODA':
    case 'INTERNACIONAL':
    case 'TV E CELEBRIDADES':
    case 'ESOTERISMO':
    case 'EMPREGOS E CONCURSOS':
    case 'HUMOR':
      return '#9475d0'

    case 'EDUCACAO':
    case 'SERIES E FILMES':
    case 'POP/ARTE':
    case 'SEXO':
      return '#ff8f79'

    case 'ALIMENTAO E SADE':
    case 'NATUREZA':
    case 'NOTCIAS':
    case 'CULINRIA':
    case 'DECORAO':
    case 'FITNESS':
    case 'ECONOMIA':
    case 'ESPORTE':
    default:
      return '#00cfb9'
  }
}



